.addProduct{
    background: #DFD7BF;
    height: 50rem;
    font-family: cursive;
}

.addProduct h1{
    padding-top: 2rem;
}

.form{
    margin-top: 5rem;
    position: relative;
    left: 18rem;
    /* left: 50%; */
    /* border: 1px solid black; */
    width: 50%;
    height: 31.5rem;
    padding: 2rem;
    background: whitesmoke;
